* {
  margin: 0;
  padding: 0;
}

.sr-product-description {
  max-width: 1480px;
  padding: 0 20px;
  margin: 0 auto;
  color: #000;
}

.sr-product-description h1, .sr-product-description h2, .sr-product-description h3, .sr-product-description h4, .sr-product-description h5, .sr-product-description h6 {
  text-transform: none;
  font-style: normal;
  letter-spacing: normal;
  line-height: normal;
  margin-bottom: 20px;
  font-family: "mont-bold", sans-serif;
}

.sr-product-description h1 {
  font-size: 40px;
  padding: 0px 80px;
  font-family: mont-heavy;
}

.sr-product-description h2 {
  font-size: 22px;
  line-height: 1.5;
}

.sr-product-description h3 {
  font-size: 18px;
}

.sr-product-description h4 {
  font-size: 16px;
}

.sr-product-description p {
  font-weight: inherit;
  font-size: 20px;
  line-height: 1.6em;
  padding: 0px 80px 20px;
  margin-bottom: 20px;
  font-family: Montserrat;
}

.sr-product-description img {
  display: block;
  width: auto;
  margin-bottom: 20px;
}

.sr-product-description .sr-pic-block {
  display: block;
  font-size: 0;
}

.sr-product-description .sr-pic-fullwidth {
  width: 100%;
}

.sr-product-description .sr-desc-card {
  font-family: "mont-bold", sans-serif;
}

.sr-product-description .sr-desc-card p {
  padding: 0 0 20px 0;
}

.sr-product-description .sr-desc-block {
  margin: 20px 0;
  padding: 20px 0;
}

.sr-product-description .sr-desc-block-center {
  text-align: center;
}

.sr-product-description .sr-desc-block-list li {
  list-style: none;
  font-size: 18px;
  font-weight: normal;
}

.sr-product-description .sr-desc-row {
  display: flex;
  flex-wrap: wrap;
  min-height: inherit;
  max-height: inherit;
  justify-content: space-between;
}

.sr-product-description .sr-desc-row > div {
  display: flex;
}

.sr-product-description .sr-desc-col-4 {
  width: calc(33.333333333333336% - 6.666666666666667px);
}

@media (max-width: 1024px) {
  .sr-product-description .sr-pic-block {
    width: 100%;
  }
  .sr-product-description .sr-desc-block {
    margin: 16px 0;
    padding: 16px 0;
  }
  .sr-product-description p {
    padding: 0 60px;
  }
}

@media (max-width: 767px) {
  .sr-product-description .sr-desc-col-4 {
    width: 100%;
  }
  .sr-product-description h1 {
    font-size: 28px;
    padding: 0 20px;
  }
  .sr-product-description p {
    font-size: 16px;
    padding: 0 20px 20px;
  }
}
